
#bg{
    background-image: url(/Images/mainbg1.jpeg);
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    height: 90vh;

}
#frame {
    background-image: url(/Images/frame.png);
    height: 90vh;
    width: 100%;
    
    background-repeat: no-repeat;
    background-size:cover;
    mix-blend-mode:darken;
    
}
main {
    z-index: 1;
}

@media (max-width: 890px) {
    #bg{
        background-image: url(/Images/mainbg.jpeg);
        background-repeat: no-repeat;
        background-size: cover;
        width: 100%;
        height: 90vh;
        display: none;
    
    }
    #frame {
        background:linear-gradient(45deg,white,wheat);
        height: 90vh;
        width: 100%;
        
        background-repeat: no-repeat;
        background-size:cover;
        mix-blend-mode:darken;
        display: none;
        
    }
}