@property --angle {
  syntax: '<angle>';
  inherits: true;
  initial-value: 0%;
}
body {
  background-color: rgb(0, 0, 0);
  
}


nav ul {
  font-size: large;
  
  list-style: none;
  background-color: transparent;
  margin-top: 0%;
  margin-left: 53%;
  box-sizing: border-box;
  font-weight: bolder;
  font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  
}
nav li{
  
  padding : 3%;
  display:inline-block;
}
nav a{
  position: relative;
  text-decoration: none;

  display: inline-block;
  padding: 0.5em 1em;
  color: aliceblue;
  z-index: 1;
  background-color: black;
 border-radius: 10%;
 box-shadow: 10px 0px 20px rgb(241, 5, 5);
  
}
nav a:hover{
  box-shadow: 10px 0px 20px rgb(241, 5, 5);
}
nav span {
  padding: 4.5% 6% 0 0 ;
  position: absolute;
  background-image: conic-gradient( rgb(5, 5, 5), rgb(248, 10, 10),rgb(5, 5, 5),rgb(253, 47, 0));
  border-radius: 5px;
  top:0%;
  
  
 
  left: 56%;
  transition: .5s;
  z-index: 0;

  
  
}


nav li:nth-child(1):hover~span {
  left : 56%;
  
 
}
nav li:nth-child(2):hover~span {
  margin-left: 7%;
  padding-left: 2%;
  opacity: 1;
}
nav li:nth-child(3):hover~span {
  margin-left :16.6%;
  padding-left: 2%;
  opacity: 1;
}
nav li:nth-child(4):hover~span {
  margin-left:26.1%;
  padding-left: 2%;
  opacity: 1;
}
nav li:nth-child(5):hover~span {
  margin-left: 35%;
  padding-left: 2%;
  opacity: 1;
}


.checkbtn {
  font-size: 22px;
  color: white;
  float: right;
  line-height: 80px;
  margin-right: 30px;
  cursor: pointer;
  display: none;}
  #check {
    display: none;
  }
  
  @media (max-width: 1023px) {
    .checkbtn {
      display: block;
      position: fixed;
      margin-left: 90%;
    

     
    }


    nav ul {
      
      
      list-style: none;
      background-color: transparent;
      
      margin-top: 0%;
      margin-left: -200%;
     
      font-weight: normal;
      
      text-align: center;
    }
    nav li{
      padding: 40% 0 0 0;
      display:block;
    }
    nav a{
      text-decoration: none;
      
      padding: 2%;
      color: aliceblue;
      background-color: black;
     border-radius: 10%;
     box-shadow: 10px 0px 20px rgb(250, 31, 2);
     
     margin-top: 10%;
     left: -10%;
     min-width: 100px;
     
      
    }
    nav span {
      display: none;
    }
    #check:checked~ul {
      margin-left: 30%;
      margin-top: 50%;
      
      /* background:linear-gradient(120deg,rgb(117, 146, 150,0.3),rgb(55, 60, 75,0.3)); */
      position: fixed;
      
    }
  
    nav {
      background: url(/Images/mainbg1.jpeg);
      background-repeat: no-repeat;
      height: 100vh;
      background-position: 20%;
      background-size: cover;
    }
 
  }
  
  